<template>
    <div class="mapTemp">
        <div ref="guangdong_map" id="guangdong_map" class="map"></div>
    </div>
</template>

<script>
  import JSON from "../../../utils/mapGd/guangdongmap.json";

  let mapdata = {
    tooltip: {
      trigger: 'item',
      formatter: '{b}<br/>{c} (p / km2)'
    },
    legend: {
      type: "plain",
      show: true,
    },
    // layoutCenter: ['40%', '50%'],//位置
    // layoutSize:'90%',//大小
    visualMap: {
      show: false,
      min: 0,
      max: 80,
      // text: ['High', 'Low'],
      realtime: false,
      calculable: true,
      inRange: {
        color: ['#84b7ff', '#84b7ff']
      }
    },
    series: [{
      type: 'map',
      showLegendSymbol: true,
      map: '广东',       //这里得跟js省份文件名一样得改
      label: {
        show: true,  //显示地图区域名称
        color: "#ffffff",
        emphasis: {
          show: true
        },
        position: 'top',
        lineHeight: 20,
        // backgroundColor: '#e2dcbf',
        // borderColor: '#8edb00',
        // borderRadius: 10,
        // borderWidth: '1',
        // padding: [2, 2, 2],
        fontSize: 15,
        fontWeight: 'normal',
        formatter: function (params) {
          // return params.name + '\n' + params.value+'\n'+params.data.title;    //地图上展示文字 + 数值
          return params.name;    //地图上展示文字 + 数值
        },

      },
      itemStyle: {
        normal: {
          // formatter: '{b}111',
          // color: '#ffffff',   //地图小圆点
          color: 'transparent',   //地图小圆点
          opacity: 1,
          borderWidth: 1,
          borderColor: '#4263c7',
        },
        emphasis: {
          areaColor: '#ffd669'
        }
      },
      zoom: 1.2,  //地图放大倍数
      data: [
        {
          name: '广州市',
          value: 100,
        },
        { name: '深圳市', value: '80' },
        { name: '珠海市', value: 0 },
        { name: '汕头市', value: 0 },
        {name: "佛山市",value: 52 },
        { name: '韶关市', value: 10 },
        { name: '河源市', value: 0 },
        { name: '梅州市', value: 30 },
        { name: '惠州市', value: 0 },
        { name: '汕尾市', value: 0 },
        { name: '东莞市', value: 0 },
        { name: '中山市', value: 10 },
        { name: '江门市', value: 0 },
        { name: '阳江市', value: 20 },
        { name: '湛江市', value: 0 },
        { name: '茂名市', value: 0 },
        { name: '肇庆市', value: 0 },
        { name: '清远市', value: 0 },
        { name: '潮州市', value: 0 },
        { name: '揭阳市', value: 0 },
        { name: '云浮市', value: 0 }
      ],
      nameMap: {},  //可以格式化data显示
    }]
  }
  export default {
    name: "mapTemp",
    components:{

    },
    props:{

    },
    data(){
      return {
        mapOption: mapdata,//地图数据
      }
    },
    mounted() {
      this.mapFuncton()
    },
    methods:{
      mapFuncton() {
        let echarts = require('echarts');
        this.mychart = echarts.init(this.$refs.guangdong_map)
        echarts.registerMap('广东', JSON);  //初始化广东地图

        this.mychart.setOption(this.mapOption);
        let mychart = echarts.init(this.$refs.guangdong_map, true)  //监听窗口变化重新加载
        window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载
        let $this = this;
        this.mychart.on('click', function (params) {
          window.console.log(params, 'params')
          $this.areaName = params.data.name;
          if ($this.radio == 1||$this.radio == 2||$this.radio == 3) {  //非正常情况下可以查看
            $this.dialogVisible = true;
            this.mychart.off('click');
          }
        });
      },
      setMap(res,keyValue){  //切换不同渠道，刷新显示地图数据
        let array = []
        for (let key in res){
          if (key===keyValue){
            array = res[key]
            break
          }
        }
        for (let i=0;i<array.length;i++){
          if (array[i].aliseName.indexOf('市')===-1){ //地区没有市的补充一个  把总客户数作为value    aliseName作为name
            array[i].name = array[i].aliseName+'市'
          }
          array[i].value = array[i].customerNum||0
        }

        this.mapOption.tooltip.formatter = function (params) {
          window.console.log(params,'kkkk')
          if (keyValue==='支付宝'||keyValue==='微信'){
            return params.name+'<br/>'+
              '总绑定数:'+ params.data.bindNum+'<br/>'+
              '总客户数:'+params.data.value+'<br/>'+
              '注册数排名:'+params.data.zcspm+'<br/>'+
              '绑定数排名:'+params.data.bdspm+'<br/>'+
              '注册数占比:'+params.data.zcszb+'<br/>'+
              '绑定数占比:'+params.data.bdszb
          }else if (keyValue==='全部'){
            return params.name+'<br/>'+
              '总绑定数:'+ params.data.bindNum+'<br/>'+
              '总客户数:'+params.data.value+'<br/>'+
              '总注册数:'+params.data.registerNum+'<br/>'
          }else {
            return params.name+'<br/>'+
              '总注册数:'+ params.data.registerNum+'<br/>'+
              '总客户数:'+params.data.value+'<br/>'+
              '注册数排名:'+params.data.zcspm+'<br/>'+
              '绑定数排名:'+params.data.bdspm+'<br/>'+
              '注册数占比:'+params.data.zcszb+'<br/>'+
              '绑定数占比:'+params.data.bdszb
          }
        }
        this.mapOption.series[0].data = array
        this.mychart.setOption(this.mapOption);
      }
    }
  }
</script>

<style scoped lang="scss">
    .mapTemp{
        width: 100%;
        display: inline-block;
        .map{
            width: 100%;height: 618px;display: inline-block;vertical-align: top;
        }
    }
</style>